export default {
	data(){
		return{
			params:{
				limit:30,
				order:"new",
				skip:0
			},
			banner:[],
			album:[],
			hasMore:true
		}
	},
	mounted(){
		//修改页面标题
		uni.setNavigationBarTitle({
			title:"专辑"
		})
		
		this.getList();
	},
	methods:{
		getList(){
			this.request({
				url:'http://157.122.54.189:9088/image/v1/wallpaper/album',
				data:this.params,
				
			})
			.then(result=>{
				console.log(result);
				if(this.banner.length===0){
					this.banner=result.res.banner;
				}
				if(result.res.album.length===0){
					this.hasMore=false;
					uni.showToast({
						title:"没有更多了",
						icon:"none"
					});
					return;
				}
				this.album=[...this.album,...result.res.album];
			})
		},
		handleToLower(){
			console.log("123");
			if(this.hasMore){
				this.params.skip+=this.params.limit;
				this.getList();
			}else{
				uni.showToast({
					title:"没有了",
					icon:"none"
				})
			}
		}
	},
}
